@import url("https://fonts.googleapis.com/css?family=Overpass");

*{
	box-sizing: border-box;
	font-family: "Overpass";
}
#container, html.embed{
	background: transparent !important;
}
/* Logo */
#logo a {
	background-image: inherit;
	min-height: inherit;
	height: inherit;
}

body{
	font-family: sans-serif;
}
/* Backgrounds */
html, #logo,
#container,
.wufoo input.text, .wufoo textarea.textarea,
.wufoo .instruct,
form li.focused {
	background: inherit;
}

/* Borders */
#container,
.wufoo .info, 
.wufoo .instruct {
	border: inherit;
}

/* Typography */
body,
.wufoo,
.wufoo .info h2,
.wufoo .info div,
.wufoo .section h3,
.wufoo .section div,
.wufoo label.desc, .wufoo legend.desc,
.wufoo li div, .wufoo li div label, .wufoo li span label,
.wufoo input.text, .wufoo textarea.textarea, .wufoo select.select, .wufoo label.choice, 
.wufoo label.choice,
.wufoo .instruct {
	background: inherit;
	color: inherit;
	font-size: inherit;
	font-family: inherit;
}

/* Drop Shadows */
#container {
	-webkit-box-shadow: inherit;
	-moz-box-shadow: inherit;
	-o-box-shadow: inherit;
	box-shadow: inherit;
}


#header{
	display: none;
}


.wufoo label.desc, .wufoo legend.desc, .wufoo li span label, form li div span{
	font-weight: 600;
	color: #00a7e4;
	font-family: "Overpass";
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 14px;
}

.wufoo li span label{
	color: #acacac;
}
.wufoo .field{
	border: 0;
	border-bottom: 1px solid #acacac;
	background-color: white;
	padding: 12px 0;
}


.wufoo label.desc > div{
	font-weight: 400;
}

.wufoo li.focused{
	background: transparent;
}

.wufoo li.focused .field{
	outline: none;
}

.wufoo li .field{
	width: 100%;
	margin: 0;
}
.wufoo .buttons{
	text-align: right;
}
.wufoo input.btTxt{
	background: #9a9c84;
	border: 0 !important;
	font-size: 14px;
	padding: 20px 40px 17px;
	color: white;
	border-radius: 0px;
	cursor: pointer;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-weight: bold;
	margin-right: 0 !important;
}


.wufoo input.btTxt{
	transition: all linear 0.1s;
}
.wufoo input.btTxt:hover{
	background: #00a7e4;
}

.confirm h2 {
	color: #00a7e4;
	border: 2px solid;
	text-transform: uppercase;
	padding: 100px;
	font-weight: bold;
	letter-spacing: 2.5px;
	font-family: "Overpass", HelveticaNeue, "Helvetica Neue", Helvetica, Arial, sans-serif;
	line-height: 1.4;
}

.wufoo li{
	width: 100%;
	padding-left: 0 !important;
	padding-right: 0 !important;
	margin-bottom: 30px;
}
form li.leftHalf, form li.rightHalf {
	width: 49% !important;
}

form .instruct {
	width: 100%;
	position: relative;
	visibility: visible;
	opacity: 1;
	left: auto !important;
	padding: 0;
	margin: 30px 0;
}

form .instruct em{
	color: #a5a9ac !important;
	font-size: 16px;
	line-height: 24px;
}

@media only screen and (max-width: 480px) {
	
	form li.leftHalf, form li.rightHalf{
		width: 100% !important;
	}
	
}